/* Reset */

@import url(//codepen.io/chrisdothtml/pen/ojLzJK.css);
// colors
$color-one: #2c3e50;
$btn-color: #2c3e50;
$btn-colors: ( "green": "#2ecc71", "blue": "#3498db", "purple": "#9b59b6", "navy": "#34495e", "orange": "#e67e22", "red": "#e74c3c");
// extenders
.animate {
  transition: all 0.3s;
}


/* Main Styles */

.main-container {
  width: 100%;
  max-width: 750px;
  margin: 0 auto;
  padding: 0 15px;
}

section {
  padding: 60px 0 61px;
  text-align: center;
  position: relative;
  h2 {
    margin-bottom: 15px;
    font-weight: normal;
    font-size: 17px;
    color: #D2D2D2;
    text-align: center;
  }
  &:not(:last-child):after {
    content: '';
    width: 70%;
    height: 1px;
    border-bottom: 1px dashed $color-one;
    opacity: .5;
    position: absolute;
    bottom: -1px;
    left: 15%;
  }
}

.btn {
  display: inline-block;
  line-height: 25px;
  margin: 8px;
  padding: 0 10px;
  font-size: 15px;
  position: relative;
  opacity: .999;
  border-radius: 3px;
  @extend .animate;
}

.btn-border-o {
  background-color: transparent;
  border: 1px solid #d0d0d0;
  color: #B8B8B8;
  &:before,
  &:after {
    content: '';
    border-style: solid;
    position: absolute;
    z-index: 5;
    border-radius: 3px;
    box-sizing: content-box;
    @extend .animate;
  }
  &:before {
    width: 0;
    height: 100%;
    border-width: 1px 0 1px 0;
    top: -1px;
    left: 0;
    transition-delay: 0.05s;
  }
  &:after {
    width: 100%;
    height: 0;
    border-width: 0 1px 0 1px;
    top: 0;
    left: -1px;
  }
  &:hover {
    &:before {
      width: 100%;
    }
    &:after {
      height: 100%;
    }
  }
  @each $name,
  $hex in $btn-colors {
    &.btn-#{$name} {
      &:before,
      &:after {
        border-color: #{$hex};
      }
      &:hover {
        color: #{$hex};
      }
    }
  }
}

