.btn-fill-vert-o {
  background-color: transparent;
  border: 1px solid #d0d0d0;
  color: #B8B8B8;
  overflow: hidden;
  &:before,
  &:after {
    content: '';
    width: 100%;
    height: 0;
    opacity: 0;
    position: absolute;
    left: 0;
    z-index: -1;
    @extend .animate;
  }
  &:before {
    top: 50%;
  }
  &:after {
    bottom: 50%;
  }
  &:hover {
    color: #fff;
    &:before,
    &:after {
      height: 50%;
      opacity: 1;
    }
  }
  @each $name,
  $hex in $btn-colors {
    &.btn-#{$name} {
      &:before,
      &:after {
        background-color: #{$hex};
      }
      &:hover {
        border-color: #{$hex};
      }
    }
  }
}

