$sub-menu-color: #ffffff;
$sub-menu-hover: #752020;
$panel-color: #C16E3E;
$panel-text: #000000;
$color: #1abc9c;
$grey: #9e9e9e;
$plum: #8e44ad;
$shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.16),
0 2px 8px 0 rgba(0, 0, 0, 0.12);
$form-width: 370px;
$form-width-form: 420px;
// Main Nav Menu
body {
    margin: 0;
    padding: 0;
}

h1,
a,
li {
    font-family: Roboto, sans-serif;
}

h1 {
    text-align: center;
    font-size: 26px;
    color: white;
    margin-top: 0;
    margin-bottom: 20px;
    background: darken($color, 4%);
    padding: 5px 10px;
    line-height: 150%;
    border-radius: 3px 3px 0 0;
    box-shadow: 0 2px 5px 1px rgba(0, 0, 0, 0.2);
}

a,
li {
    color: darken($grey, 20%);
    text-decoration: none;
}

.nav {
    margin: 0;
    > li {
        display: inline-block;
        &:hover {
            background: lighten($grey, 30%);
        }
        a {
            display: inline-block;
            padding: 1em 18px;
        }
    }
}
.username1{
  padding: 1em 18px;
}

.drop {
    position: relative;
    ul {
        position: absolute;
        left: 0;
        top: 3em;
        padding-left: 0;
        transition: all 0.3s ease;
        transform: scale(0);
        transform-origin: 0 0;
        box-shadow: $shadow;
        li {
            background-color: #fff;
            display: block;
            width: 100%;
            a {
                width: 100%;
                padding: 1em 18px;
                display: inline-block;
                white-space: pre;
                box-sizing: border-box;
                &:hover {
                    background: lighten($grey, 30%);
                }
            }
        }
    }
    &:hover {
        ul {
            transform: scale(1);
        }
    }
}

//---------------------------------------------
.help-block strong,
.help-block {
    font-size: 11px;
    padding: 0 25px;
    color: red;
}

.navbar-header {
    text-align: center;
    a {
        padding: 10px;
        display: inline-block;
    }
}

//----------------------
.username {
    float: right;
}

@media screen and (max-width: 800px) {
    .follow {
        margin: 400px auto 0;
    }
}

// end nav menu
// sign up (add user form )
h1,
input::-webkit-input-placeholder,
button {
    font-family: 'roboto', sans-serif;
    transition: all 0.3s ease-in-out;
}

form {
    box-sizing: border-box;
    width: $form-width-form;
    margin: 40px auto 10px;
    box-shadow: 2px 2px 5px 1px rgba(0, 0, 0, 0.5);
    padding: 1px 0 40px 0;
    border-radius: 3px;
    background-color: rgba(0, 0, 0, 0.33);
    h1 {
        box-sizing: border-box;
        padding: 20px;
    }
}

input,
select {
    margin: 40px 25px;
    width: $form-width;
    display: block;
    border: none;
    padding: 10px 0;
    border-bottom: solid 1px $color;
    transition: all 0.8s cubic-bezier(.64, .09, .08, 1);
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 96%, $color 4%);
    background-position: -400px 0;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    color: darken($color, 20%);
    &:focus,
    &:valid {
        box-shadow: none;
        outline: none;
        background-position: 0 0;
        &::-webkit-input-placeholder {
            color: $color;
            font-size: 11px;
            transform: translateY(-20px);
            visibility: visible !important;
        }
    }
}

button {
    border: none;
    background: $color;
    cursor: pointer;
    border-radius: 3px;
    padding: 6px;
    width: 200px;
    color: white;
    margin-left: 25px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.2);
    &:hover {
        transform: translateY(-3px);
        box-shadow: 0 6px 6px 0 rgba(0, 0, 0, 0.2);
    }
}

// end sign up input form
// add user and remove in group page icon styling
.follow,
.follow-single {
    width: 42px;
    height: 42px;
    border-radius: 50px;
    background: #03A9F4;
    display: inline-block;
    white-space: nowrap;
    padding: 13px;
    box-sizing: border-box;
    color: white;
    transition: all 0.2s ease;
    font-family: Roboto, sans-serif;
    text-decoration: none;
    box-shadow: 0 5px 6px 0 rgba(0, 0, 0, 0.2);
    i {
        margin-right: 20px;
        transition: margin-right 0.2s ease;
    }
    span {
        font-size: 0;
    }
}

.follow {
    &:hover {
        width: 134px;
        transform: translateX(-50px);
        i {
            margin-right: 10px;
        }
        span {
            font-size: 14px;
        }
    }
}

.follow-single {
    width: 37px;
    height: 37px;
    padding: 9px 13px;
    &:hover {
        background-color: $color;
    }
}

.user-operation {
    text-align: center;
    margin-bottom: 30px;
}

.center-content {
    text-align: center;
    button {
        margin-left: 0;
    }
}

.sub-menu {
    width: 50%;
    display: inline-block;
    padding-left: 40px;
    a {
        text-decoration: none;
        color: $sub-menu-color;
        &:hover {
            color: $sub-menu-hover;
        }
    }
}

.rember-label {
    display: block;
    margin-bottom: 30px;
    font-size: 15px;
    color: $color;
    input {
        margin: 0;
        width: 60px;
        display: inline-block;
    }
}

.forgot {
    font-size: 12px;
    display: block;
    margin: 26px 0 0 26px;
    color: $color
}

//-------------------------------
// modal form
.modal {
    form {
        width: 100%;
        margin: 0;
        box-shadow: none;
        padding: 0;
        border-radius: 0;
        background: transparent;
    }
}

.close {
    position: absolute;
    right: 16px;
    top: 7px;
    font-size: 34px;
    color: #960000;
}

.text-info {
    color: #338A58;
}

.alert {
    display: block;
    padding: 15px;
    background: rgba(40, 131, 177, 0.20);
    color: rgb(40, 131, 177);
}

.alert-danger {
    background: rgba(181, 12, 12, 0.20);
    color: rgb(130, 0, 0);
}

.alert-success {
    background: rgba(46, 204, 113, 0.20);
    color: rgb(3, 88, 71);
}

.back {
    background-image: url(http://theartmad.com/wp-content/uploads/Dark-Grey-Texture-Wallpaper-5.jpg);
    background-size: 100% 100%;
    background-attachment: fixed;
    background-position: top center;
    //max-height: 100%;
}
.full{
  height: 100%;
}

.delete-group {
    .fa-trash {
        font-size: 20px;
        color: #7D0000;
        padding: 10px;
    }
    .fa-check {
        font-size: 20px;
        padding: 10px;
        color: #18AA8D;
    }
}

.input-group-mix{
  position: relative;
  span{
    position: absolute;
    left: 25px;
    top: 5.7px;
    color: #FFFFFF;
    vertical-align: middle;
    width: 30px;
    height: 30px;
    background-color: #1ABC9C;
    line-height: 2.2;
    text-align: center;
  }
  input{
    padding-left: 40px;
    width: 330px
  }
}

.bg-blue{
  background-color: #215A7E;
}